<template>
    <div class="login">
        <el-card class="box-card">
            <h1></h1>
            <el-form ref="form" :model="form" label-width="80px" label-position="top" :rules="rules">
                <el-form-item label="" prop="name">
                    <el-input v-model="form.name" ref="name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pwd">
                    <el-input v-model="form.pwd" ref="pwd"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit('form')"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { getUser } from "../../api";
import { valid } from 'semver';
import {mapActions} from 'vuex';
import $axios from 'axios';

export default {
    data() {
      return {
        form:{
          name: '',
          pwd: ''
        },
        datas:[null],
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 5, message: ' 1  5 ', trigger: 'blur' }
          ],
          pwd: [
            { required: true, message: '', trigger: 'change' },
            { min: 4, max: 12, message: ' 4  12 ', trigger: 'blur' }
          ]
        }
      }
    },

    methods: {

      onSubmit(formName) {
        console.log('!');
        this.$refs[formName].validate((valid) =>{
          if (valid) {
            this.userSelect();
            console.log('onsubmit');
          } else {
            console.log('onsubmit');
            return false;
          }
        });
      },

      async userSelect(){
        console.log('userselect');
        let {data} = await getUser().post('/dologin',{username:this.$refs.name.value,password:this.$refs.pwd.value}).catch(()=>{
          this.$message({message: '',type: 'error'});
        });
        this.datas = data;
        if(this.datas===[]){
          console.log('');
        }else if(this.datas[0].username == this.$refs.name.value && this.datas[0].password == this.$refs.pwd.value){
          let user = {username,userstate};
          user.username = this.datas[0].username;
          user.userstate = true;
          this.setLogin(user);
          this.$message({message: '',type: 'success'});
          this.$router.push({path:'/'});
        }else{
          this.$message({message: '',type: 'error'});
        }
        console.log('userselect');
      },

      ...mapActions([
        'setLogin'
      ])

    }
}
</script>

<style scoped>
.login{
    width: 100%;
    height: 700px;
    padding-top: 5%;
    margin: 0;
    display: block;
}

.box-card {
    width: 450px;
    height: 450px;
    display: block;
    margin-left: auto;
    margin-right:auto; 
    padding: 0;
  }
</style>



